.home {
  @include page-grid;

  &__title {
    grid-column: 1/-1;

    @include on-tablet {
      grid-column: 1/4;
    }

    @include on-desktop {
      grid-column: span 5;
    }
  }

  &__info {
    font-size: 23px;
    font-weight: 500;
    line-height: 32.2px;

    grid-column: 1/-1;

    @include on-tablet {
      grid-column: 4/-1;
    }

    @include on-desktop {
      grid-column: span 6;
    }
  }
}
